:root {
  --primary: #0a192f;
  --secondary: #64ffda;
  --navy: #0a192f;
  --light-navy: #112240;
  --lightest-navy:  #233554;
  --slate: #8892b0;
  --light-slate: #a8b2d1;
  --lightest-slate: #ccd6f6;
  --gray: rgb(26, 26, 26);
  --white: #e6f1ff;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--navy);
}

input, textarea {
  border: none;
  border-radius: 5px;
  padding: 10px;
}

input:focus, textarea:focus {
  outline: none;
}

.row {
  display: flex;
  flex-direction: row;
}

.col {
  display: flex;
  flex-direction: column;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

p {
  padding: 0;
  margin: 0;
}

.form-control {
  color: white;
  border: var(--light-slate);
}

.form-control::placeholder {
  color: white;
}

.form-control:focus {
  border-color: var(--secondary);
  box-shadow: 1px 1px 1px var(--secondary);
}

.primary-btn {
  background-color: transparent;
  font-size: 16px;
  font-weight: 500;
  width: max-content;
  color: var(--secondary);
  border: 1px solid var(--secondary);
  border-radius: 3px;
  padding: 10px;
  transition-duration: 500ms;
  text-decoration: none;
}

.primary-btn:hover {
  cursor: pointer;
  box-shadow: 4px 4px 1px 1px var(--secondary);
  border-radius: 3px;
  margin-top: -5px;
  margin-left: -5px;
  transition-duration: 300ms;
}

.card {
  border: 1px solid var(--secondary);
  border-radius: 5px;
}
